@import '../../Components/Styles/index.scss';


.faq {
    background:$color-text-three;
    
    &__container {
        display: flex;
        flex-direction: column;
        color: $color-text-five;
        padding: 0px 3px
    }

    &__title {
        @extend %font-h2;
        color: $color-text-five;
        margin: 50px 0px;
    }

    

    &__block {
        display: flex;
        gap: 8px;
    }

    &__wrap-foto {
       flex: 1 0 52%;
    }

    &__foto{
        width: 100%;
        height: 100%;
        object-fit: cover;
    }

    &__block-text {
        
    }

    &__subtitle {
        @extend  %font-h3;
        margin: 10px 0px;
        text-align: center;
    }

    &__text {
        @extend  %font-text-main;
    }

}